<template>
  <b-container class="px-0">
    <b-card no-body>
      <b-card-body class="px-md-5 pt-3 mt-2 mobile-scroll">
        <!-- Recruit logo starts here -->
        <div class="my-md-3 md-0 mobile-logo">
          <img src="/img/recruitly/logo.svg" alt="logo" class="img-fluid" />
        </div>
        <!-- Recruit logo ends here -->
        <!-- Filters starts here -->
        <template v-if="jobs_data.length > 10 || enable_search">
          <b-row class="px-2 mt-4 mb-3">
            <!-- Global search starts here -->
            <b-col md="12" class="px-2">
              <!-- Search Filter Options starts here -->
              <b-input-group>
                <template #prepend>
                  <b-input-group-text>
                    <i class="fe fe-search" />
                  </b-input-group-text>
                </template>
                <!-- Search by Job Title starts here -->
                <b-form-input class="w-25" placeholder="Search by job title or description" v-model="search_data"></b-form-input>
                <!-- Search by Job Title ends here -->

                <!-- Search by Location starts here -->
                <b-form-select v-model="selected_location" :options="locations_options" text-field="text" value-field="value" @change="getFilteredJobs()">
                  <template #first>
                    <b-form-select-option :value="null">Location</b-form-select-option>
                  </template>
                </b-form-select>
                <!-- Search by Location ends here -->

                <!-- Search by Job Type starts here -->
                <b-form-select :options="job_type" v-model="selected_job_type" text-field="text" value-field="value" @change="getFilteredJobs()" v-if="job_type.length > 1">
                  <template #first>
                    <b-form-select-option :value="null">Job Types - All</b-form-select-option>
                  </template>
                </b-form-select>
                <!-- Search by Job Type ends here -->
              </b-input-group>
              <!-- Search Filter Options ends here -->
            </b-col>
            <!-- Global search ends here -->
          </b-row>
        </template>
        <!-- Loading icon starts here -->
        <div class="mx-auto py-4 text-center" v-if="loading">
          <b-spinner variant="primary" label="Spinning" />
        </div>
        <!-- Loading icon ends here -->
        <!-- Empty state starts here -->
        <b-row class="px-2 mt-4">
          <b-col md="12" class="px-2" v-if="resultant_data.length <= 0 && !loading">
            <div class="dashed-border py-6 mb-3 rounded">
              <div class="d-flex justify-content-center align-items-center">
                <div class="text-center">
                  <b-img src="/img/no-jobs-found.svg" class="img-fluid mb-4 pb-2 pt-2" />
                  <h2 class="mb-2 font-weight-bold">No Jobs Listed</h2>
                  <p class="text-muted mb-0 white-space">
                    Currently, no open positions are available in this organization.
                  </p>
                </div>
              </div>
            </div>
          </b-col>
          <b-col md="12" class="px-2" v-for="(data, index) in resultant_data" :key="index">
            <b-card class="mb-3 shadow-none">
              <div class="mb-md-3">
                <!-- Job title starts here -->
                <b-link :href="`/recruithire/${data.id}`" target="_blank" class="text-hover-primary">
                  <h4 class="font-weight-bold line-height-1-4 mb-2 text-truncate text-body">{{ data.job_title }}</h4>
                </b-link>
                <!-- Description starts here -->
                <div class="mt-3">
                  <!-- Job description starts here -->
                  <p class="mb-3 align-middle text-gray-800">
                    <span v-html="shortDescription(data.description)" />
                    <!-- Read less description data starts here -->
                    <!--<span v-if="!validate_read_more[`description_${data.id}`]" v-html="readDescription(data.description, 'read_less')" />-->
                    <!-- Read less description data ends here -->

                    <!-- Read more description data starts here -->
                    <!--<span v-if="validate_read_more[`description_${data.id}`]" v-html="readDescription(data.description, 'read_more')" />-->
                    <!-- Read more description data ends here -->

                    <!-- Read more and less options starts here -->
                    <!--<b-link class="text-primary ml-1 cursor-pointer" v-if="data.description.length > 1000">
                      <span v-if="validate_read_more[`description_${data.id}`]" @click="$set(validate_read_more, `description_${data.id}`, false)" title="Read less description">Read less</span>
                      <span v-else-if="!validate_read_more[`description_${data.id}`]" @click="$set(validate_read_more, `description_${data.id}`, true)" title="Read more description">Read more</span>
                    </b-link>-->
                    <!-- Read more and less options ends here -->
                  </p>
                  <!-- Job description ends here -->
                </div>
                <!-- Description ends here -->
                <div class="mb-2 pb-1 d-flex flex-column align-items-md-center flex-md-row flex-wrap">
                  <div class="text-muted mr-md-5 text-nowrap mb-2 mt-n2" v-if="data.no_of_position" style="line-height:1">
                    <span class="text-muted pr-2 mr-1">No of Positions :</span>
                    <span class="ml-n3 position-relative" style="top:2px">
                      <i class="fe fe-user" style="font-size: 11px !important;position: relative;top: -3px !important;left: 14px !important;" />
                      <i class="fe fe-search" style="font-size:19px !important" />
                      <span class="text-body position-relative" style="top: -2px !important;left: 1px !important;">{{ data.no_of_position }}</span>
                    </span>
                  </div>
                  <div class="text-muted mr-md-5 text-nowrap mb-2" v-if="data.no_of_applicants">
                    <span class="text-muted pr-2 mr-1">Total Applicants :</span>
                    <i class="fe fe-users mr-2" />
                    <span class="text-body">{{ data.no_of_applicants }}</span>
                  </div>
                  <!-- Location starts here -->
                  <div class="text-muted mr-md-5 text-nowrap mb-2" v-if="data.location">
                    <span class="text-muted pr-2 mr-1">Job Locaton :</span>
                    <i class="fe fe-map-pin mr-2" />
                    <span class="text-body">{{ data.location }}</span>
                  </div>
                  <div class="text-muted mr-md-5 text-nowrap mb-2" v-if="data.type">
                    <span class="text-muted pr-2 mr-1">Job Type :</span>
                    <i class="fe fe-briefcase mr-2" />
                    <span class="text-body">{{ data.type }}</span>
                  </div>
                </div>
              </div>
              <!-- Job title ends here -->
              <div class="d-flex flex-column flex-md-row justify-content-md-between align-items-md-end align-items-baseline mt-n2 mt-md-0">
                <small class="text-muted" v-if="data.created_date">
                  <span>Posted on :</span>
                  <i class="fe fe-calendar pl-2 pr-1" />
                  <span class="text-body">
                    {{ getLocalTime(data.created_date) }}
                  </span>
                </small>

                <div class="d-flex align-items-center">
                  <!-- Share Job option starts here -->
                  <b-button variant="link" size="sm" class="mr-3" @click="openShareJobModal(data)">
                    <i class="fe fe-share-2 mr-2" />
                    <span>Share</span>
                  </b-button>
                  <!-- Share Job option ends here -->

                  <!-- Apply Job option starts here -->
                  <b-button @click="openModal(data)" size="sm" class="px-3 ml-auto text-nowrap mt-5 mt-md-0" variant="outline-secondary">
                    <i class="fe fe-check-circle mr-1" />
                    Apply
                  </b-button>
                </div>
                <!-- Apply Job option ends here -->
              </div>
            </b-card>
          </b-col>
        </b-row>
        <!-- empty state ends here -->
      </b-card-body>
      <div class="footer-branding py-3 px-4 text-right border-top">
        <span>Powered by</span>
        <b-link target="_blank" href="http://500apps.com/" class="ml-1">
          <img src="/img/favicon.webp" class="ml-2" style="width: 17px;" />
          500apps
        </b-link>
      </div>
      <!-- Apply candidate sidebar starts here -->
      <b-sidebar
        id="add_candidate"
        title="Apply"
        right
        shadow
        backdrop
        header-class="d-flex flex-row-reverse justify-content-between bg-white close-btn-right border-bottom"
        width="420px"
        body-class="bg-soft-light"
      >
        <form ref="form" @submit.prevent="addCandidate">
          <b-form-group label="Name" label-class="pb-1" class="mb-3 pb-2">
            <b-form-input placeholder="Please enter your name" type="text" v-model="form.name" required />
          </b-form-group>
          <b-form-group label="Email" label-class="pb-1" class="mb-3 pb-2">
            <b-form-input placeholder="Please enter your email" type="email" v-model="form.email" required />
          </b-form-group>
          <b-form-group label="Resume" label-class="pb-1" class="mb-3 pb-2">
            <b-form-file
              @submit.stop.prevent
              @input="uploadResume"
              ref="file_input"
              accept=".doc,.docx,.pdf,.txt,.png, .ppt,.rtf,.tex,.jpg,.jpeg,.xls,.xlsx,.csv"
              class="mb-2"
              placeholder="Please upload your resume"
              drop-placeholder="Drop file here..."
              v-model="form.resume_upload"
            ></b-form-file>
            <span class="text-muted"> Supported files formats are .doc, .docx, .pdf, .txt, .png, .ppt, .rtf, .tex, .jpg, .jpeg, .xls, .xlsx and .csv </span>
          </b-form-group>

          <b-form-group label="Primary Contact" label-class="pb-1" class="mb-3 pb-2 mobile-number">
            <VueTelInput type="tel" name="telephone" v-model="form.primary_contact" mode="international" autocomplete="tel" :key="render_phone" />
          </b-form-group>
          <b-form-group class="recaptcha-mr-0" v-if="recaptcha_site_key && recaptcha_secret_key">
            <Recaptcha :site_key="recaptcha_site_key" :secret_key="recaptcha_secret_key" />
            <span v-if="validate_recaptcha" class="text-danger">Please select or configure recaptcha</span>
          </b-form-group>
          <div class="d-flex align-items-center justify-content-end border-top pt-4 mt-3">
            <b-badge variant="soft-danger" class="font-weight-500 py-2 px-3 rounded-sm" v-if="error_message">Duplicate entry</b-badge>
            <b-button variant="primary" class="px-5 ml-4" type="submit">{{ submit_button }}</b-button>
          </div>
          <b-badge variant="soft-success" class="font-weight-500 py-2 px-3 mt-2 rounded-sm" v-if="submit_button == 'Submitted'"
            >Thank you {{ form.name }} for applying to the {{ selected_job_title }}.</b-badge
          >
        </form>
      </b-sidebar>
      <!-- Apply candidate sidebar ends here -->

      <!-- Share job sidebar starts here -->
      <b-sidebar
        id="share-job"
        title="Share Job"
        right
        shadow
        backdrop
        header-class="d-flex flex-row-reverse justify-content-between bg-white close-btn-right border-bottom"
        width="420px"
        body-class="bg-soft-light"
      >
        <form ref="form" @submit.prevent="sendEmail('career-page')">
          <b-form-group label="To Email" label-class="pb-1" class="mb-3 pb-2">
            <b-form-input placeholder="Please enter your email" type="email" v-model="share_to_email" required />
          </b-form-group>
          <div class="d-flex align-items-center justify-content-end border-top pt-4 mt-3">
            <b-badge variant="soft-success" class="font-weight-500 py-2 px-3 rounded-sm" v-if="send_email_status">Sent Successfully</b-badge>
            <b-button variant="primary" class="px-5 ml-4" type="submit">{{ send_email_button_status }}</b-button>
          </div>
        </form>
      </b-sidebar>
      <!-- Share job sidebar starts here -->
    </b-card>
  </b-container>
</template>
<script>
  import JobDetails from "@/js/recruit/job-details";
  import Candidate from "@/js/recruit/candidate";
  import FileManager from "@/js/core/file-manager";
  import DefaultData from "@/js/recruit/default-data";
  import SendEmailData from "@/js/recruit/send-email-data";
  import { VueTelInput } from "vue-tel-input";
  import Recaptcha from "../form/components/input/Recaptcha";
  import RecaptchaDetails from "@/js/recruit/get-recaptcha-details";

  export default {
    components: { VueTelInput, Recaptcha },
    data() {
      return {
        domain_id: "",
        group_id: "",
        job_type: [],
        selected_location: null,
        selected_job_type: null,
        locations_options: [],
        jobdetails: [],
        jobs_data: [],
        form: {
          name: "",
          email: "",
          resume_upload: "",
          primary_contact: "",
        },
        enable_delete_file: false,
        recaptcha_response_status: false,
        validate_recaptcha: false,
        submit_button: "Submit",
        error_message: false,
        render: 0,
        search_data: "",
        validate_read_more: {},
        loading: true,
        enable_search: false,
        sub_domain: "",
        share_to_email: "",
        row_data: {},
        send_email_status: false, // Show email success message
        send_email_button_status: "send", // Change button content send,sending and sent.
        selected_job_title: "",
        render_phone: 0,
        recaptcha_site_key: "",
        recaptcha_secret_key: "",
      };
    },
    computed: {
      // Get active jobs and search data
      resultant_data() {
        if (this.search_data) {
          return this.jobs_data.filter((data) => {
            return this.search_data
              .toLowerCase()
              .split(" ")
              .every(
                (value) =>
                  data.job_title.toLowerCase().includes(value) ||
                  data.description
                    .replace(/(<([^>]+)>)/g, "")
                    .toLowerCase()
                    .includes(value),
              );
          });
        } else return this.jobs_data;
      },
    },
    async mounted() {
      let host = window.location.host;
      if (host.includes(".recruithire.com")) await this.checkShortcutUrl();
      this.domain_id = window.domain;
      this.group_id = window.project_id ? window.project_id : window.user ? window.user : "";
      this.sub_domain = this.$route.query.s ? this.$route.query.s : "";
      this.getActiveJobs();
    },
    methods: {
      // Check shortcut url
      async checkShortcutUrl() {
        let shortcutData;
        let host = window.location.host;
        shortcutData = await JobDetails.getShortcutData(host.split(".recruithire.com")[0]);
        if (!shortcutData || !shortcutData.length) return;
        window.domain = shortcutData[0].domain_id;

        // Set group_id and id if the shortcut url is group level
        if (shortcutData[0].link == "group") {
          if (shortcutData[0].data_1) {
            window.project_id = shortcutData[0].data_1 ? JSON.parse(shortcutData[0].data_1).group_id : null;
            window.id = null;
          }
        } else {
          window.project_id = shortcutData[0].data_1 ? JSON.parse(shortcutData[0].data_1).group_id : null;
          window.id = null;
        }
        window.region = shortcutData[0].env;
        window.user = shortcutData[0].link == "group" ? shortcutData[0].created_by : null;
      },
      // Clear the search results
      async clearSearch() {
        this.selected_location = null;
        this.selected_job_type = null;
        this.search_data = "";
        this.getFilteredJobs();
      },
      // Get Jobs by Location and Job-Type
      async getFilteredJobs() {
        this.enable_search = true;
        let param = "";
        if (this.selected_location) param = `?location=${this.selected_location}`;
        if (this.selected_job_type) param = `?job_type=${this.selected_job_type}`;
        if (this.selected_location && this.selected_job_type) param = `?location=${this.selected_location}&job_type=${this.selected_job_type}`;
        try {
          if (this.sub_domain) this.jobs_data = await JobDetails.getFilteredJobs(this.domain_id, "", param, this.sub_domain);
          else this.jobs_data = await JobDetails.getFilteredJobs(this.domain_id, this.group_id, param, "");
        } catch (error) {
          this.jobs_data = [];
        }
      },
      // Get all active jobs
      async getActiveJobs() {
        try {
          // Get active job by domain
          if (this.sub_domain) this.jobs_data = await JobDetails.getDomainJobs(this.domain_id, this.sub_domain);
          // Get active job by group id and domain id
          else this.jobs_data = await JobDetails.getActiveJobs(this.domain_id, this.group_id);
        } catch (error) {
          this.loading = false;
        }
        this.loading = false;
        // Get job types
        if (this.jobs_data.length > 0) {
          let job_types = [];
          // Get unique job type from active jobs data
          this.jobs_data.filter(function(item) {
            let data_index = job_types.findIndex((data) => data.text == item.type);
            if (data_index <= -1 && item.type != null) job_types.push({ text: item.type, value: item.type });
            return job_types;
          });
          // Assign unique job types
          this.job_type = job_types;
        }

        // Get only locations
        this.jobs_data.forEach((data) => {
          if (data.location) this.locations_options.push({ text: data.location, value: data.location });
        });

        // Get Unique locations
        if (this.locations_options.length > 0) {
          this.locations_options = this.locations_options.reduce((acc, current) => {
            const location = acc.find((item) => item.text.toLowerCase() === current.text.toLowerCase());
            if (!location) {
              current["text"] = current["text"].charAt(0).toUpperCase() + current["text"].slice(1);
              return acc.concat([current]);
            } else return acc;
          }, []);
        }
      },
      // Remove html tags from job description text
      getDescription(description) {
        return description.replace(/(<([^>]+)>)/g, "");
      },
      // Open add candidate modal
      async openModal(data) {
        ++this.render_phone;
        this.submit_button = "Submit";
        this.error_message = false;
        // Open candidate side bar
        this.$root.$emit("bv::toggle::collapse", "add_candidate");
        // Get recaptcha data
        this.$root.$on("recaptcha-callback-response", this.getRecaptcha);
        this.form = {};
        this.form.job_id = data.id;
        this.group_id = data.group_id;
        this.selected_job_title = data.job_title;
        this.enable_delete_file = false;

        // Get plugins recaptcha info (Ref: get-recaptcha-details.js )
        let response = await RecaptchaDetails.getRecaptchaKeys(".recruithire.com", "recruit", this.domain_id, "recruithire", this.group_id);
        if (response) {
          this.recaptcha_site_key = response.recaptcha_site_key;
          this.recaptcha_secret_key = response.recaptcha_secret_key;
        }
      },
      // Get uploaded resume URL
      async uploadResume(file) {
        // Get s3 url after uploading resume upload in add candidate formSendEmailData
        let upload_data = await FileManager.upload(`recruit/${this.group_id}`, "post-object", file, "", file.type, "public-read");
        let data = upload_data.presign_data;
        // Assign s3 url to form
        this.form.resume_upload = `${data.url}${data.fields.key}`;
      },
      // Add a candidate
      async addCandidate() {
        // check recaptcha response is true or not
        if (this.recaptcha_response_status) {
          this.submit_button = "Submitting";
          this.form.source = "website";
          try {
            // Split name with empty-space and assign to respective fields
            let name = this.form.name.split(" ");
            this.form.first_name = name[0] ? name[0] : "";
            this.form.last_name = name[1] ? name[1] : "";

            let candidate_response = await Candidate.saveCandidate(this.domain_id, this.group_id, this.form);
            // Check candidate is added or not and if yes, close add candidate sidebar
            if (candidate_response.status) {
              this.submit_button = "Submitted";
              setTimeout(() => {
                ++this.render;
                this.$root.$emit("bv::toggle::collapse", "add_candidate");
                this.submit_button = "Submit";
              }, 3000);
            }
          } catch (error) {
            ++this.render;
            this.submit_button = "Submit";
            this.error_message = true;
          }
        } else this.validate_recaptcha = true;
      },
      // Get recaptcha response
      getRecaptcha(response) {
        // Get response after selecting recaptcha in add candidate form
        this.recaptcha_response_status = response ? true : false;
        // Show validation message if recaptcha status is false
        this.validate_recaptcha = this.recaptcha_response_status ? false : true;
      },
      // Read more or less description
      readDescription(data, type) {
        return type == "read_more" ? data : data.substring(0, 1000) + (data.length < 1000 ? "" : "...");
      },
      // Get local time
      getLocalTime(value) {
        SendEmailData;
        return DefaultData.dateFormat(value);
      },
      shortDescription(description) {
        // description.replace(/(<([^>]+)>)/g, "").trim().length
        return description.length < 160 ? description : description.substring(0, 160) + (description.length < 160 ? "" : "...");
      },
      // Open share job model
      openShareJobModal(data) {
        this.share_to_email = "";
        this.row_data = data;
        this.$root.$emit("bv::toggle::collapse", "share-job");
      },
      ...SendEmailData, // Ref: send-email-data.js
    },
  };
</script>
<style>
  @import url("/css/sidebar/sidebar.css");
  .font-weight-500 {
    font-weight: 500;
  }
</style>
